body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    background: #f5f5f5;
    color: #1d1d1d;
}

.hero-news {
    position: relative;
    width: 100%;
    height: 400px;
    overflow: hidden;
    text-align: center;

    .hero-img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
    }

    .overlay {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.5);
    }

    .hero-content {
        position: relative;
        z-index: 1;
        max-width: 600px;
        margin: 0 auto;
        padding: 80px 20px;
        color: #fff;

        h1 {
            font-size: 32px;
            font-weight: 800;
        }

        p {
            font-size: 18px;
            margin-top: 10px;
        }

        .btn {
            display: inline-block;
            margin-top: 10px;
            padding: 10px 20px;
            background: #FFD700;
            color: #1d1d1d;
            text-decoration: none;
            border-radius: 5px;
            font-weight: bold;
        }
    }
}

.news-section {
    padding: 40px 0;

    .container {
        width: 90%;
        max-width: 1200px;
        margin: 0 auto;
    }

    h2 {
        font-size: 28px;
        font-weight: 800;
        margin-bottom: 20px;
    }

    .row {
        display: flex;
        flex-wrap: wrap;
        gap: 20px;
    }

    .col-md-8 {
        flex: 2;
    }

    .col-md-4 {
        flex: 1;
    }

    .news-card {
        background: #1d1d1d;
        color: #fff;
        border-radius: 8px;
        overflow: hidden;
        margin-bottom: 20px;
        box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);

        img {
            width: 100%;
            height: 200px;
            object-fit: cover;
        }

        .news-content {
            padding: 20px;

            h3 {
                font-size: 18px;
                font-weight: 700;
            }

            p {
                font-size: 16px;
                margin-top: 10px;
            }

            a {
                display: inline-block;
                margin-top: 10px;
                color: #FFD700;
                text-decoration: none;
                font-weight: bold;
            }
        }
    }

    .recent-news {
        .recent-news-card {
            display: flex;
            align-items: center;
            background: #1d1d1d;
            color: #fff;
            border-radius: 8px;
            overflow: hidden;
            margin-bottom: 15px;
            padding: 10px;

            img {
                width: 80px;
                height: 60px;
                object-fit: cover;
                margin-right: 10px;
            }

            p {
                font-size: 14px;
                margin: 0;
            }
        }
    }
}

@media (max-width: 768px) {
    .row {
        flex-direction: column;
    }

    .col-md-8, .col-md-4 {
        width: 100%;
    }
}